// components/AdminDashboard.tsx
import Link from 'next/link';
import { motion } from 'framer-motion';
import { 
  FiTrendingUp as TrendingUpIcon,
  FiTrendingDown as TrendingDownIcon,
  FiMinus as MinusIcon 
} from 'react-icons/fi';

const AdminDashboard = () => {
  const stats = [
    { name: 'Total Cars', value: '24', change: '+2', changeType: 'positive' },
    { name: 'Active Requests', value: '16', change: '+5', changeType: 'negative' },
    { name: 'Pending Complaints', value: '8', change: '+1', changeType: 'negative' },
    { name: 'Available Drivers', value: '12', change: '0', changeType: 'neutral' },
  ];

  // Animation variants
  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: { staggerChildren: 0.1 }
    }
  };

  const itemVariants = {
    hidden: { y: 20, opacity: 0 },
    visible: {
      y: 0,
      opacity: 1,
      transition: { duration: 0.5 }
    }
  };

  const quickActions = [
    { href: "/tms-modules/admin/car-management/manage-cars", emoji: "ðŸš˜", text: "Manage Cars" },
    { href: "/tms-modules/admin/request-management/service-requests", emoji: "ðŸ“‹", text: "Service Requests" },
    { href: "/tms-modules/admin/complaint-management/view-complaints", emoji: "ðŸ“¢", text: "View Complaints" },
    { href: "/tms-modules/admin/reports/generate-reports", emoji: "ðŸ“Š", text: "Generate Reports" }
  ];

  const recentActivities = [1, 2, 3, 4];

  return (
    <div className="space-y-6 p-4">
      {/* Dashboard Title */}
      <motion.h1 
        initial={{ opacity: 0, x: -20 }}
        animate={{ opacity: 1, x: 0 }}
        transition={{ duration: 0.5 }}
        className="text-2xl font-bold text-gray-800"
      >
        Dashboard Overview
      </motion.h1>
      
      {/* Stats Cards */}
      <motion.div
        className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6"
        variants={containerVariants}
        initial="hidden"
        animate="visible"
      >
        {stats.map((stat, index) => (
          <motion.div
            key={`stat-${index}`}
            variants={itemVariants}
            whileHover={{ y: -5 }}
            className="bg-white rounded-xl shadow p-6 hover:shadow-lg transition-all"
          >
            <div className="flex justify-between items-start">
              <div>
                <p className="text-sm font-medium text-gray-500">{stat.name}</p>
                <motion.p 
                  className="mt-1 text-3xl font-semibold text-gray-900"
                  whileHover={{ scale: 1.05 }}
                >
                  {stat.value}
                </motion.p>
              </div>
              <motion.span 
                className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${
                  stat.changeType === 'positive' ? 'bg-green-100 text-green-800' : 
                  stat.changeType === 'negative' ? 'bg-red-100 text-red-800' : 
                  'bg-gray-100 text-gray-800'
                }`}
                whileHover={{ scale: 1.1 }}
              >
                {stat.change}
                {stat.changeType === 'positive' && <TrendingUpIcon className="ml-1" />}
                {stat.changeType === 'negative' && <TrendingDownIcon className="ml-1" />}
                {stat.changeType === 'neutral' && <MinusIcon className="ml-1" />}
              </motion.span>
            </div>
          </motion.div>
        ))}
      </motion.div>

      {/* Quick Actions */}
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 0.2 }}
      >
        <motion.h2 
          className="text-xl font-bold text-gray-800 mb-4"
          whileHover={{ x: 5 }}
        >
          Quick Actions
        </motion.h2>
        <motion.div
          className="grid grid-cols-2 md:grid-cols-4 gap-4"
          variants={containerVariants}
        >
          {quickActions.map((action, index) => (
            <motion.div
              key={`action-${index}`}
              variants={itemVariants}
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
              <Link href={action.href} passHref legacyBehavior>
                <a>
                  <div className="bg-white rounded-lg shadow p-4 text-center cursor-pointer hover:bg-gray-50 transition-all">
                    <motion.div 
                      className="text-2xl mb-2"
                      animate={{ 
                        y: [0, -5, 0],
                        rotate: [0, 5, -5, 0]
                      }}
                      transition={{ 
                        duration: 3,
                        repeat: Infinity,
                        ease: "easeInOut"
                      }}
                    >
                      {action.emoji}
                    </motion.div>
                    <p className="font-medium">{action.text}</p>
                  </div>
                </a>
              </Link>
            </motion.div>
          ))}
        </motion.div>
      </motion.div>

      {/* Recent Activities and Charts */}
      <motion.div
        className="grid grid-cols-1 lg:grid-cols-3 gap-6"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 0.4 }}
      >
        <motion.div 
          className="lg:col-span-2 bg-white rounded-xl shadow p-6"
          whileHover={{ boxShadow: "0 10px 25px -5px rgba(0, 0, 0, 0.1)" }}
        >
          <motion.h2 
            className="text-xl font-bold text-gray-800 mb-4"
            whileHover={{ scale: 1.01 }}
          >
            Request Statistics
          </motion.h2>
          <div className="h-64 bg-gray-100 rounded-lg flex items-center justify-center">
            <motion.p 
              className="text-gray-500"
              animate={{ opacity: [0.6, 1, 0.6] }}
              transition={{ duration: 2, repeat: Infinity }}
            >
              Request Statistics Chart
            </motion.p>
          </div>
        </motion.div>
        
        <motion.div 
          className="bg-white rounded-xl shadow p-6"
          whileHover={{ boxShadow: "0 10px 25px -5px rgba(0, 0, 0, 0.1)" }}
        >
          <motion.h2 
            className="text-xl font-bold text-gray-800 mb-4"
            whileHover={{ scale: 1.01 }}
          >
            Recent Activities
          </motion.h2>
          <div className="space-y-4">
            {recentActivities.map((item) => (
              <motion.div
                key={`activity-${item}`}
                className="border-b border-gray-200 pb-4 last:border-0 last:pb-0"
                whileHover={{ x: 5 }}
              >
                <p className="text-sm text-gray-600">New service request from Employee #{item}</p>
                <motion.p 
                  className="text-xs text-gray-400 mt-1"
                  whileHover={{ scale: 1.05 }}
                >
                  10 minutes ago
                </motion.p>
              </motion.div>
            ))}
          </div>
        </motion.div>
      </motion.div>
    </div>
  );
};

export default AdminDashboard;